:root {
    --color-bg-light: #f4f4f4;
    --color-bg-dark: #1e1e1e;
    --color-text-light: #000;
    --color-text-dark: #fff;
    --color-accent: #808080;
  }
  
  .sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 250px;
    height: 100vh;
    background-color: var(--color-bg-light);
    color: var(--color-text-light);
    transition: width 0.3s ease, background-color 0.3s ease;
  }
  
  .sidebar.dark {
    background-color: var(--color-bg-dark);
    color: var(--color-text-dark);
  }
  
  .sidebar.collapsed {
    width: 70px;
  }
  
  .toggleButton {
    background: none;
    border: none;
    color: inherit;
    font-size: 1.5rem;
    padding: 10px;
    cursor: pointer;
  }
  
  .menu {
    flex-grow: 1;
    padding: 20px;
  }
  
  .title {
    margin-bottom: 20px;
    font-size: 1.2rem;
    font-weight: bold;
  }
  
  .menuList {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .menuList li {
    margin-bottom: 15px;
  }
  
  .link {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: inherit;
    font-size: 1rem;
    transition: color 0.3s ease;
  }
  
  .link:hover {
    color: var(--color-accent);
  }
  
  .icon {
    margin-right: 10px;
    font-size: 1.2rem;
  }
  
  .footer {
    padding: 20px;
    border-top: 1px solid var(--color-accent);
  }
  
  .themeToggle {
    display: flex;
    align-items: center;
    gap: 10px;
    background: none;
    border: none;
    color: inherit;
    cursor: pointer;
    font-size: 1rem;
  }
  